import React, {useEffect, useState} from 'react'

import {Button} from '../../Forms'
import CatalogueItem from '../CatalogueItem'
import Loading from '../../Loading'
import {merge, set} from 'lodash'

import ListQuestion from './ListQuestion'
import TextQuestion from './TextQuestion'

import './Questions.scss'

const {REACT_APP_IS_DEVELOPMENT} = process.env

function Questions({answers, selected, setAnswers, setResults, setSelected}) {
  const [questions, setQuestions] = useState([])
  const [errors, setErrors] = useState([])
  const [loading, setLoading] = useState(true)
  const [status, setStatus] = useState(-1)

  useEffect(() => {
    handleOnConfigure()
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  const handleOnCancel = evt => {
    evt.preventDefault()

    setAnswers({})
    setSelected(null)
  }

  const handleOnChange = evt => {
    const {name, value} = evt.target

    const updated = {}
    set(updated, name, value.trim())
    const merged = merge(answers, updated)

    setAnswers(merged)
  }

  const handleOnConfigure = async evt => {
    if (evt) evt.preventDefault()

    setLoading(true)

    let host = window.location.origin
    if (REACT_APP_IS_DEVELOPMENT) host = host.replace(':3000', ':5000')

    try {
      const response = await fetch(
        `${host}/api/generate/configure?id=${
          selected.id
        }&details=${JSON.stringify(answers)}`,
      )
      const json = await response.json()

      setErrors(JSON.parse(json.errors))
      setQuestions(JSON.parse(json.questions))
      setStatus(json.status)
    } catch (error) {
      setErrors([{error}])
    }

    setLoading(false)
  }

  const handleOnImport = async evt => {
    evt.preventDefault()

    setLoading(true)

    let host = window.location.origin
    if (REACT_APP_IS_DEVELOPMENT) host = host.replace(':3000', ':5000')

    try {
      const response = await fetch(
        `${host}/api/generate/create?id=${selected.id}&details=${JSON.stringify(
          answers,
        )}`,
      )
      const result = await response.json()

      if (result.error) {
        setErrors(['An unknown error occurred'])
      } else {
        setResults(true)
      }
    } catch (error) {
      setErrors([{error}])
    }

    setLoading(false)
  }

  return (
    <>
      <CatalogueItem disabled item={selected} />
      <hr />
      {loading ? (
        <Loading />
      ) : (
        <>
          {errors.length ? (
            <div className="generator-errors">
              <ul>
                {errors.map((error, index) => (
                  <li key={`error_${index}_${error}`}>{error}</li>
                ))}
              </ul>
            </div>
          ) : null}
          {status === -1 ? (
            <form className="form" onSubmit={handleOnConfigure}>
              {questions.map(question => (
                <>
                  {['checkbox', 'list'].includes(question.type) ? (
                    <ListQuestion
                      answers={answers}
                      handleOnChange={handleOnChange}
                      question={question}
                    />
                  ) : (
                    <TextQuestion
                      answers={answers}
                      handleOnChange={handleOnChange}
                      question={question}
                      type={question.type || 'text'}
                    />
                  )}
                </>
              ))}
              <div className="buttons field">
                <Button disabled={loading} type="submit">
                  Configure
                </Button>
                <Button
                  className="is-link"
                  onClick={handleOnCancel}
                  type="button"
                >
                  Cancel
                </Button>
              </div>
            </form>
          ) : null}
          {status === 0 || status === 1 ? (
            <form className="form" onSubmit={handleOnImport}>
              {status === 0 ? (
                <p>
                  {selected.name} will be validated at query time. Click
                  "Generate" to generate a schema.
                </p>
              ) : null}
              {status === 1 ? (
                <p>
                  Successfully connected to {selected.name}. Click "Generate" to
                  generate a schema.
                </p>
              ) : null}
              <Button disabled={loading} type="submit">
                Generate
              </Button>
              <Button
                className="is-link"
                onClick={handleOnCancel}
                type="button"
              >
                Cancel
              </Button>
            </form>
          ) : null}
        </>
      )}
    </>
  )
}

export default Questions
