import {buildClientSchema, getIntrospectionQuery} from 'graphql'
import GraphiQL from 'graphiql'
import CodeExporter from 'graphiql-code-exporter'
import GraphiQLExplorer from 'graphiql-explorer'
import {useCallback, useEffect, useMemo, useState} from 'react'
import {useLocation, useParams} from 'react-router-dom'

import Generator from '../Generator'
import Loading from '../Loading'
import Redirection from '../Redirection'
import ServerStatus from '../ServerStatus'
import SettingsPanel from '../SettingsPanel'
import SettingsToggle from '../SettingsToggle'

import localStorage from '../../helpers/localStorage'
import snippets from '../../helpers/snippets'

import logo from '../../assets/images/logo.svg'
import './Explorer.scss'

const {REACT_APP_IS_DEVELOPMENT} = process.env

function Explorer() {
  const {folder, name} = useParams()
  const search = useLocation().search

  const storage = useMemo(() => localStorage(folder, name), [folder, name])

  const [details, setDetails] = useState(null)
  const [explorerIsOpen, setExplorerIsOpen] = useState(
    storage.getItem('is-explorer-open') === 'true',
  )
  const [exporterIsOpen, setExporterIsOpen] = useState(
    storage.getItem('is-exporter-open') === 'true',
  )
  const [isError, setIsError] = useState(false)
  const [generatorIsOpen, setGeneratorIsOpen] = useState(
    storage.getItem('is-generator-open') === 'true',
  )
  const [settingsIsOpen, setSettingsIsOpen] = useState(
    storage.getItem('is-settings-open') === 'true',
  )
  const [graphiQL, setGraphiQL] = useState(null)
  const [query, setQuery] = useState(null)
  const [schema, setSchema] = useState(null)
  const [serverAvailable, setServerAvailable] = useState(true)

  let host = window.location.origin
  if (REACT_APP_IS_DEVELOPMENT) host = host.replace(':3000', ':5000')

  useEffect(() => {
    const PING_INTERVAL = 5000

    const interval = setInterval(async () => {
      try {
        await fetch(`${host}/api/ping`)
        setServerAvailable(true)
        console.log('stepzen start running')
      } catch (err) {
        setServerAvailable(false)
        console.log('stepzen start NOT running')
      }
    }, PING_INTERVAL)

    return () => {
      clearInterval(interval)
    }
  }, [])

  const fetcher = useCallback(
    graphQLParams => {
      fetch(`${host}/api/details`)
        .then(response => response.json())
        .then(result => {
          if (!storage.getItem('graphiql:queries') && result.samples) {
            setQuery(result.samples)
          }
          setDetails(result)
        })

      return fetch(`${host}/api/graphql`, {
        method: 'post',
        headers: {'Content-Type': 'application/json'},
        body: JSON.stringify(graphQLParams),
      }).then(response => response.json())
    },
    [host, storage],
  )

  useEffect(() => {
    document.title = `StepZen - ${folder}/${name}`
    fetcher({
      query: getIntrospectionQuery(),
    })
      .then(result => {
        if (result.data) {
          setSchema(buildClientSchema(result.data))
        } else {
          setIsError(true)
        }
      })
      .catch(() => setIsError(true))
  }, [fetcher, folder, name])

  // This useEffect sets what the user sees in the Query panel
  // - if you pass a query through the URL, it will set that
  // - Otherwise it will look in history (which is in localStorage)
  // - Otherwise it will see if there are any samples
  // - Lastly it will display our static message content
  useEffect(() => {
    ;(async () => {
      // This is us trying to set from ?query=
      const params = new URLSearchParams(search)
      if (params.get('query')) {
        setQuery(params.get('query'))
        return
      }

      // This is us trying to set from any query history
      const history = storage.getItem('graphiql:queries')
      if (history) {
        const json = JSON.parse(history)
        const [last] = json.queries.slice(-1)
        setQuery(last.query)
        return
      }

      // This is us seeing if there are any samples available
      const response = await fetch(`${host}/api/samples`)
      if (response.ok) {
        const {samples} = await response.json()
        if (samples) {
          setQuery(samples)
          return
        }
      }

      // No query param, no history, no samples. Set a predefined message
      setQuery(`# Welcome to StepZen!
# ===================
#
# - Explore your current schema by selecting "Explorer"
# - Generate React components by selecting "Export"
#
# Read more at http://stepzen.com/docs
#
`)
    })()
  }, [host, search, storage])

  const handleEditQuery = query => {
    setQuery(query)
  }

  const handleToggleExplorer = () => {
    const isOpen = !explorerIsOpen
    setExplorerIsOpen(isOpen)
    storage.setItem('is-explorer-open', isOpen)
  }

  const handleToggleExporter = () => {
    const isOpen = !exporterIsOpen
    setExporterIsOpen(isOpen)
    storage.setItem('is-exporter-open', isOpen)
  }

  const handleToggleGenerator = () => {
    const isOpen = !generatorIsOpen
    setGeneratorIsOpen(isOpen)
    storage.setItem('is-generator-open', isOpen)
  }

  const handleToggleSettings = () => {
    const isOpen = !settingsIsOpen
    setSettingsIsOpen(isOpen)
    storage.setItem('is-settings-open', isOpen)
  }

  if (isError) {
    return (
      <div className="graphiql-container">
        <div className="graphiql-container">
          <Redirection />
          <Generator onClose={handleToggleGenerator} />
          <div className="error-or-loading">
            <img src={logo} alt="StepZen" className="logo" />
            <p>
              Could not load <code>{details.endpoint}</code>
            </p>
          </div>
        </div>
      </div>
    )
  }

  return schema ? (
    <div className="graphiql-container">
      <Redirection />
      {serverAvailable ? null : <ServerStatus></ServerStatus>}
      {generatorIsOpen ? (
        <Generator onTogglePanel={handleToggleGenerator} />
      ) : null}
      {explorerIsOpen ? (
        <GraphiQLExplorer
          explorerIsOpen={explorerIsOpen}
          query={query}
          onEdit={handleEditQuery}
          onRunOperation={operationName =>
            graphiQL.handleRunQuery(operationName)
          }
          onToggleExplorer={handleToggleExplorer}
          schema={schema}
        />
      ) : null}
      <div className="graphiql-container">
        <GraphiQL
          fetcher={fetcher}
          query={query}
          onEditQuery={handleEditQuery}
          ref={ref => setGraphiQL(ref)}
          schema={schema}
          storage={storage}
        >
          <GraphiQL.Logo>
            <img src={logo} alt="StepZen" />
          </GraphiQL.Logo>
          {details ? (
            <GraphiQL.Footer>
              <p className="endpoint">
                {details.endpoint.replace('.io', '.net')}
              </p>
            </GraphiQL.Footer>
          ) : null}
          <GraphiQL.Toolbar>
            <GraphiQL.Button
              onClick={handleToggleExplorer}
              label="Explorer"
              title="Toggle Explorer"
            />
            <GraphiQL.Button
              onClick={() => graphiQL.handlePrettifyQuery()}
              label="Prettify"
              title="Prettify Query (Shift-Ctrl-P)"
            />
            <GraphiQL.Button
              onClick={() => graphiQL.handleToggleHistory()}
              label="History"
              title="Show History"
            />

            {/* <GraphiQL.Button onClick={handleToggleGenerator} label="Import" title="Toggle Import" /> */}
            {/* When we get all the use cases working there we can revisit this button. But for now let's hide it since it is not entirely correct. */}
            <GraphiQL.Button
              onClick={handleToggleExporter}
              label="Export"
              title="Toggle Export"
            />
            {settingsIsOpen ? null : (
              <SettingsToggle onClick={handleToggleSettings} />
            )}
          </GraphiQL.Toolbar>
        </GraphiQL>
      </div>
      {exporterIsOpen ? (
        <CodeExporter
          codeMirrorTheme="default"
          hideCodeExporter={handleToggleExporter}
          query={query}
          snippets={snippets}
        />
      ) : null}
      {settingsIsOpen ? (
        <SettingsPanel onTogglePanel={handleToggleSettings} />
      ) : null}
    </div>
  ) : (
    <div className="graphiql-container">
      <Loading />
    </div>
  )
}

export default Explorer
