import Toggle from 'react-toggle'
import {useEffect, useState} from 'react'
import * as yaml from 'yaml'

import {Button, TextArea} from '../Forms'

import './PredicatesToggle.scss'
import 'react-toggle/style.css'

const {REACT_APP_IS_DEVELOPMENT} = process.env

function PredicatesToggle() {
  const [available, setAvailable] = useState(true)
  const [enabled, setEnabled] = useState(false)
  const [error, setError] = useState()
  const [loading, setLoading] = useState(true)

  const [predicatesHeaders, setPredicatesHeaders] = useState('')

  let host = window.location.origin
  if (REACT_APP_IS_DEVELOPMENT) host = host.replace(':3000', ':5000')

  useEffect(() => {
    ;(async () => {
      const response = await fetch(`${host}/api/predicates`)
      const {predicates} = await response.json()

      setAvailable(predicates.available)
      setEnabled(predicates.enabled)
      setLoading(false)
    })()
  })

  const updatePredicates = async checked => {
    setLoading(true)

    const response = await fetch(`${host}/api/predicates`, {
      body: JSON.stringify({
        enabled: checked,
        headers: yaml.parse(predicatesHeaders),
      }),
      headers: {
        'Content-Type': 'application/json',
      },
      method: 'POST',
    })

    const {predicates} = await response.json()

    setEnabled(predicates.enabled)
    setLoading(false)
  }

  const handleOnChange = async evt => {
    const {checked} = evt.target

    setEnabled(checked)

    await updatePredicates(checked)
  }

  const handleOnChangePredicatesHeaders = async evt => {
    const {value} = evt.target

    setPredicatesHeaders(value)
  }

  const handleUpdateSettings = async evt => {
    evt.preventDefault()

    try {
      yaml.parse(predicatesHeaders)

      setError(null)
      setLoading(true)

      await updatePredicates(enabled)
    } catch {
      setError('Headers must be valid JSON or YAML')
    }
  }

  return available ? (
    <div className="predicates-toggle">
      <label>
        <span>Rulesets / custom headers are {enabled ? 'ON' : 'OFF'}</span>
        <Toggle
          checked={enabled}
          disabled={loading}
          onChange={handleOnChange}
        />
      </label>
      {enabled ? (
        <div className="predicates-settings">
          <form className="form" onSubmit={handleUpdateSettings}>
            {error ? error : null}
            <p>Please enter any authentication headers, e.g.</p>
            <pre>{'{"Authorization": "Apikey [token]"}'}</pre>
            <TextArea
              id="predicates-headers"
              label="Headers"
              name="predicates-headers"
              onChange={handleOnChangePredicatesHeaders}
              required={false}
              rows={12}
              value={predicatesHeaders}
            />
            <hr />
            <Button type="submit">Update</Button>
          </form>
        </div>
      ) : null}
    </div>
  ) : null
}

export default PredicatesToggle
