import {FontAwesomeIcon} from '@fortawesome/react-fontawesome'
import {faList, faThLarge} from '@fortawesome/free-solid-svg-icons'
import {useEffect, useState} from 'react'

import Catalogue from './Catalogue'
import Error from '../Error'
import GraphiQLPanel from '../GraphiQLPanel'
import Loading from '../Loading'
import Questions from './Questions'
import Results from './Results'

import './Generator.scss'

const {REACT_APP_IS_DEVELOPMENT} = process.env

function Generator({onTogglePanel}) {
  const [answers, setAnswers] = useState({})
  const [catalogue, setCatalogue] = useState()
  const [error, setError] = useState(false)
  const [loading, setLoading] = useState(true)
  const [results, setResults] = useState()
  const [selected, setSelected] = useState()

  const startView = window.localStorage.getItem('generator-view') || 'list'
  const [view, setView] = useState(startView)

  const handleSwitchView = (evt, value) => {
    evt.preventDefault()

    setView(value)
    window.localStorage.setItem('generator-view', value)
  }

  useEffect(() => {
    ;(async () => {
      let host = window.location.origin
      if (REACT_APP_IS_DEVELOPMENT) host = host.replace(':3000', ':5000')

      try {
        const response = await fetch(`${host}/api/generate/engines`)
        const json = await response.json()

        setCatalogue(json)
        setLoading(false)
      } catch (error) {
        setError(true)
      }
    })()
  }, [])

  return (
    <GraphiQLPanel
      body={
        <>
          {error ? <Error text="Could not load code generators" /> : null}
          {loading && !error ? <Loading /> : null}
          {!loading && !error ? (
            <>
              {!selected ? (
                <Catalogue
                  catalogue={catalogue}
                  onSelect={setSelected}
                  view={view}
                />
              ) : null}
              {selected && !results ? (
                <Questions
                  answers={answers}
                  selected={selected}
                  setAnswers={setAnswers}
                  setResults={setResults}
                  setSelected={setSelected}
                />
              ) : null}
              {results ? (
                <Results
                  onToggleGenerator={onTogglePanel}
                  results={results}
                  selected={selected}
                  setAnswers={setAnswers}
                  setResults={setResults}
                  setSelected={setSelected}
                />
              ) : null}
            </>
          ) : null}
        </>
      }
      footer={
        selected ? null : (
          <ul className="generator-view-switcher">
            <li className="generator-view-switcher-view">
              <button
                className={`generator-view-switcher-switch ${
                  view === 'list' ? 'is-selected' : ''
                }`}
                onClick={evt => handleSwitchView(evt, 'list')}
                type="button"
              >
                <FontAwesomeIcon icon={faList} />
                <span>List</span>
              </button>
            </li>
            <li className="generator-view-switcher-view">
              <button
                className={`generator-view-switcher-switch ${
                  view === 'grid' ? 'is-selected' : ''
                }`}
                onClick={evt => handleSwitchView(evt, 'grid')}
                type="button"
              >
                <FontAwesomeIcon icon={faThLarge} />
                <span>Grid</span>
              </button>
            </li>
          </ul>
        )
      }
      className="is-generators"
      onTogglePanel={onTogglePanel}
      title="Schema Templates"
    ></GraphiQLPanel>
  )
}

export default Generator
