import {useEffect} from 'react'
import {useHistory, useParams} from 'react-router-dom'

const {REACT_APP_IS_DEVELOPMENT} = process.env

function Redirection() {
  const {folder, name} = useParams()
  const history = useHistory()

  useEffect(() => {
    ;(async () => {
      let host = window.location.origin
      if (REACT_APP_IS_DEVELOPMENT) host = host.replace(':3000', ':5000')

      const response = await fetch(`${host}/api/details`)
      const json = await response.json()

      const {endpoint} = json.workspace
      const parts = endpoint.split('/')

      if (parts[0] !== folder || parts[1] !== name) {
        history.push(`/${endpoint}`)
      }
    })()
  }, [folder, history, name])
  return null
}

export default Redirection
