const localStorage = (folder, name) => ({
  getItem: key => {
    return window.localStorage.getItem(`${folder}-${name}-${key}`)
  },
  removeItem: key => {
    return window.localStorage.removeItem(`${folder}-${name}-${key}`)
  },
  setItem: (key, value) => {
    return window.localStorage.setItem(`${folder}-${name}-${key}`, value)
  },
})

export default localStorage
