const Button = ({
  children,
  className = '',
  disabled = false,
  id,
  onClick = () => {},
  type = 'button',
}) => (
  <button
    className={`button ${className}`}
    disabled={disabled}
    type={type}
    onClick={onClick}
    id={id}
  >
    {children}
  </button>
)

export default Button
