const Checkbox = ({
  checked = false,
  disabled = false,
  id,
  label,
  name,
  onChange = () => {},
  readonly = false,
  type = 'checkbox',
  value = '',
}) => (
  <div className="checkbox field">
    {label ? (
      <label>
        <input
          defaultChecked={checked}
          disabled={disabled}
          onChange={onChange}
          id={id || name}
          name={name}
          readOnly={readonly}
          type={type}
          value={value}
        />
        {label}
      </label>
    ) : (
      <input
        defaultChecked={checked}
        disabled={disabled}
        onChange={onChange}
        id={id || name}
        name={name}
        readOnly={readonly}
        type={type}
        value={value}
      />
    )}
  </div>
)

export default Checkbox
