import {Button} from '../../Forms'
import CatalogueItem from '../CatalogueItem'

function Results({
  onToggleGenerator,
  results,
  selected,
  setAnswers,
  setResults,
  setSelected,
}) {
  const handleOnBack = evt => {
    evt.preventDefault()

    setResults()
  }

  const handleOnDone = evt => {
    evt.preventDefault()

    setAnswers({})
    setResults()
    setSelected()

    onToggleGenerator()
  }

  return (
    <div className="form">
      <CatalogueItem disabled item={selected} />
      <hr />
      {results.error ? (
        <>
          <div className="generator-errors">
            <ul>
              <li>
                An error occurred. Please check your console for more
                information.
              </li>
            </ul>
          </div>
          <Button onClick={handleOnBack} className="is-link">
            Back
          </Button>
        </>
      ) : (
        <>
          <p>Successfully generated your schema</p>
          <Button onClick={handleOnDone}>Close</Button>
        </>
      )}
    </div>
  )
}

export default Results
