const TextArea = ({
  disabled = false,
  id,
  label,
  name,
  onChange = () => {},
  placeholder = '',
  readonly = false,
  required = true,
  rows = 3,
  type = 'text',
  value,
}) => (
  <div className="field">
    <label htmlFor={id || name}>{label}</label>
    <textarea
      defaultValue={value}
      disabled={disabled}
      onChange={onChange}
      name={name}
      id={id || name}
      type={type}
      placeholder={placeholder}
      readOnly={readonly}
      required={required}
      rows={rows}
    />
  </div>
)

export default TextArea
