import './Categories.scss'

function Categories({categories, category, setCategory}) {
  const handleOnChange = evt => {
    const {value} = evt.target
    setCategory(value)
  }

  return (
    <form className="generator-categories">
      <ul className="list">
        <li className="list-item">
          <p className="label">Filter: </p>
        </li>
        <li className="list-item" key={`generator_category_all`}>
          <input
            defaultChecked={!category}
            id={`generator_category_all`}
            name="generator_category"
            onChange={handleOnChange}
            type="radio"
            value=""
          />
          <label htmlFor={`generator_category_all`}>All</label>
        </li>
        {categories.map(item => (
          <li className="list-item" key={`generator_category_${item}`}>
            <input
              defaultChecked={category === item}
              id={`generator_category_${item}`}
              name="generator_category"
              onChange={handleOnChange}
              type="radio"
              value={item}
            />
            <label htmlFor={`generator_category_${item}`}>{item}</label>
          </li>
        ))}
      </ul>
    </form>
  )
}

export default Categories
