import {TextArea, TextField} from '../../../Forms'

function TextQuestion({answers, handleOnChange, question}) {
  return question.type === 'textarea' ? (
    <TextArea
      label={question.message}
      name={question.name}
      onChange={handleOnChange}
      rows={10}
      type={question.type}
      value={answers[question.name]}
    />
  ) : (
    <TextField
      label={question.message}
      name={question.name}
      onChange={handleOnChange}
      type={question.type}
      value={answers[question.name]}
    />
  )
}

export default TextQuestion
