const TextField = ({
  disabled = false,
  id,
  label,
  name,
  onChange = () => {},
  placeholder = '',
  readonly = false,
  required = true,
  type = 'text',
  value,
}) => (
  <div className="field">
    {label ? <label htmlFor={id || name}>{label}</label> : null}
    <input
      defaultValue={value}
      disabled={disabled}
      onChange={onChange}
      name={name}
      id={id || name}
      type={type}
      placeholder={placeholder}
      readOnly={readonly}
      required={required}
    />
  </div>
)

export default TextField
