import {useState} from 'react'

import CatalogueItem from '../CatalogueItem'
import Categories from '../Categories'

import './Catalogue.scss'

function Catalogue({catalogue, onSelect, view = 'list'}) {
  let categories = []
  for (const item of catalogue) {
    const cats = JSON.parse(item.categories)
    for (const cat of cats) {
      if (categories.includes(cat) === false) categories.push(cat)
    }
  }
  categories.sort((a, b) => (a.toLowerCase() > b.toLowerCase() ? 1 : -1))

  const [category, setCategory] = useState()

  const filtered = () => {
    return !category
      ? catalogue
      : catalogue.filter(item => {
          const cats = JSON.parse(item.categories)
          return cats.includes(category)
        })
  }

  return (
    <div className={`catalogue is-${view}`}>
      <Categories
        categories={categories}
        category={category}
        setCategory={setCategory}
      />
      <ul className="catalogue-item-list">
        {filtered().map((item, index) => (
          <li className="catalogue-item" key={`catalogue_item_${index}`}>
            <CatalogueItem
              index={index}
              item={item}
              onSelect={onSelect}
              view={view}
            />
          </li>
        ))}
      </ul>
    </div>
  )
}

export default Catalogue
