import {Checkbox} from '../../../Forms'

function ListQuestion({answers, handleOnChange, question}) {
  const handleOnChangeCheckbox = evt => {
    const {checked, name, value} = evt.target

    const current = answers[name]?.split(',') || []

    const answer = checked
      ? current.concat(value).join(',')
      : current.filter(item => item !== value).join(',')

    const simulated = {
      target: {
        name: question.name,
        value: answer,
      },
    }

    handleOnChange(simulated)
  }

  return (
    <div className="field">
      <p>{question.message}</p>
      {question.type === 'checkbox' ? (
        <>
          {question.choices.map(choice => (
            <Checkbox
              label={choice}
              name={question.name}
              id={`${question.name}_${choice}`}
              onChange={handleOnChangeCheckbox}
              value={choice}
              type="checkbox"
            />
          ))}
        </>
      ) : null}
      {question.type === 'list' ? (
        <>
          {question.choices.map(choice => (
            <Checkbox
              label={choice}
              name={question.name}
              id={`${question.name}_${choice}`}
              onChange={handleOnChange}
              value={choice}
              type="radio"
            />
          ))}
        </>
      ) : null}
    </div>
  )
}

export default ListQuestion
