const getQuery = (arg, spaceCount) => {
  const {operationDataList} = arg
  const {query} = operationDataList[0]
  const anonymousQuery = query.replace(/query\s.+{/gim, `{`)
  return (
    ` `.repeat(spaceCount) +
    anonymousQuery.replace(/\n/g, `\n` + ` `.repeat(spaceCount))
  )
}

const apolloQuery = {
  name: `useQuery hook`,
  language: `Apollo`,
  codeMirrorMode: `jsx`,
  options: [],
  generate: arg => `import { gql, useQuery } from '@apollo/client';

const GET_QUERY = gql\`
  query MyQuery ${getQuery(arg, 2)}
\`;

function ComponentName() {
  const { loading, error, data } = useQuery(GET_QUERY);

  if (loading) return <p>Loading ...</p>;

  if (error) return (
    <pre>{JSON.stringify(error, null, 2)}</pre>
  );

  return (
    <pre>{JSON.stringify(data, null, 2)}</pre>
  );
}

export default ComponentName

`,
}

const gatsbyPageQuery = {
  name: `Page query`,
  language: `Gatsby`,
  codeMirrorMode: `jsx`,
  options: [],
  generate: arg => `import React from "react"
import { graphql } from "gatsby"

const ComponentName = ({ data }) => <pre>{JSON.stringify(data, null, 4)}</pre>

export const query = graphql\`
${getQuery(arg, 2)}
\`

export default ComponentName

`,
}

const gatsbyStaticHook = {
  name: `StaticQuery hook`,
  language: `Gatsby`,
  codeMirrorMode: `jsx`,
  options: [],
  generate: arg => `import React from "react"
import { useStaticQuery, graphql } from "gatsby"

const ComponentName = () => {
const data = useStaticQuery(graphql\`
${getQuery(arg, 4)}
\`)
return <pre>{JSON.stringify(data, null, 4)}</pre>
}

export default ComponentName

`,
}

const gatsbyStaticQuery = {
  name: `StaticQuery`,
  language: `Gatsby`,
  codeMirrorMode: `jsx`,
  options: [],
  generate: arg => `import React from "react"
import { StaticQuery, graphql } from "gatsby"

const ComponentName = () => (
<StaticQuery
  query={graphql\`
${getQuery(arg, 6)}
  \`}
  render={data => <pre>{JSON.stringify(data, null, 4)}</pre>}
></StaticQuery>
)

export default ComponentName

`,
}

const snippets = [
  apolloQuery,
  gatsbyPageQuery,
  gatsbyStaticHook,
  gatsbyStaticQuery,
]

export default snippets
