function CatalogueItem({
  disabled = false,
  item,
  index = 'standalone',
  onSelect = () => {},
  view = 'list',
}) {
  return (
    <button
      className={`catalogue-card is-${view}`}
      disabled={disabled}
      htmlFor={`catalogue_item_${index}`}
      onClick={() => onSelect(item)}
    >
      <div className="catalogue-card-image">
        <img alt={`${item.name} logo`} src={item.image} />
      </div>
      <div className="catalogue-card-content">
        <h3 className="catalogue-card-title">{item.name}</h3>
        {item.description ? (
          <p className="catalogue-card-text">{item.description}</p>
        ) : null}
      </div>
    </button>
  )
}

export default CatalogueItem
